import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Swap',
    component:  () => import(/* webpackChunkName: "pools" */ '../views/Swap.vue')
  },
  /*
  {
    path: '/pools',
    name: 'Pools',
    component:  () => import( webpackChunkName: "pools"  '../views/Pools.vue') },*/
  {
    path: '/swap',
    name: 'Swap',
    component:  () => import(/* webpackChunkName: "pools" */ '../views/Swap.vue')
  },
  {
    path: '/debug',
    name: 'debug',
    component: () => import(/* webpackChunkName: "debug" */ '../views/Debug.vue')
  },
  {
    path: '/pool',
    name: 'Pool',
    component: () => import(/* webpackChunkName: "pool" */  '../views/Pool.vue')
  },
  {
    path: '/me',
    name: 'Me',
    component:() => import( /* webpackChunkName: "me" */ '../views/Me.vue')
  },
  {
    path: '/farm',
    name: 'Farm',
    component:() => import( /* webpackChunkName: "farm" */ '../views/Farm.vue')
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
