 <template>

   <div class="hidden" >

     <vs-navbar shadow square center-collapsed v-model="active" style="background-color:#E91E63;position: relative;">
       <template #left>
         <vs-button @click="activeSidebar = !activeSidebar" flat icon  color="#ffffff">
           <i class='bx bx-menu' style="color:#ffffff;"></i>
         </vs-button>
       </template>
       <template #right>

         <el-dropdown  v-if="!!account"  @command="switchBalance">
         <vs-button icon textWhite style="background-color: rgba(0,0,0,0);border:1px solid #ffffff;line-height:19px;" gradient>{{ balance}}<i class='bx bx-down-arrow'></i></vs-button>

           <el-dropdown-menu slot="dropdown" >
             <el-dropdown-item command="CFX" >CFX</el-dropdown-item>
             <el-dropdown-item command="PCFX" >PCFX</el-dropdown-item>
           </el-dropdown-menu>
         </el-dropdown>
         <vs-button v-if="!!account" textWhite icon style="background-color: rgba(0,0,0,0);border:1px solid #ffffff;">{{ shortAddr(account) }}<i class='bx bxs-user'></i> </vs-button>
         <vs-button v-else icon style="background-color: rgba(0,0,0,0);border:1px solid #ffffff;"textWhite  @click="login()">{{$t('unlock')}}<i class='bx bx-lock-open'></i></vs-button>

         <el-dropdown @command="switchLanguage">
           <vs-button icon style="background-color: rgba(0,0,0,0);border:1px solid #ffffff;" textWhite><img :src="'language-'+$t('language')+'.svg'" style="width:18px;"></img><i class='bx bx-down-arrow'></i></vs-button>

           <el-dropdown-menu slot="dropdown" >
             <el-dropdown-item command="en" >English</el-dropdown-item>
             <el-dropdown-item command="zh" ></el-dropdown-item>
           </el-dropdown-menu>
         </el-dropdown>

       </template>
     </vs-navbar>

     <vs-navbar square  centerCollapsed text-white style="z-index:2000;position: relative;background: #000000;">
       <template #left>
            <img src="logo.png" class="logo-size">
       </template>
       <template #right>
<!--         <vs-navbar-item :active="$route.path == '/'" id="guide" to="/" style="width:70px;text-align:center;">-->
<!--           {{$t('home')}}-->
<!--         </vs-navbar-item>-->

         <vs-navbar-item :active="$route.path == '/swap' || $route.path == '/'" id="docs" to="/swap" style="width:70px;text-align:center;">
           {{$t('swap')}}
         </vs-navbar-item>
         <vs-navbar-item :active="$route.path == '/farm'" id="guide" to="/farm" style="width:70px;text-align:center;">
           {{$t('farm')}}
         </vs-navbar-item>
         <!--<vs-navbar-item :active="$route.path == '/me'" id="license" to="/me" style="width:70px;text-align:center;">
           {{$t('me')}}
         </vs-navbar-item>-->
       </template>
     </vs-navbar>
     <vs-sidebar
         text-white
         style="color:#E91E63"
         background="rgb(26,24,28)"
         v-model="active"
         :open.sync="activeSidebar"
     >
       <template #logo>
         <!-- ...img logo -->
       </template>
       <vs-sidebar-item id="home" to="/">
         <template #icon>
           <i class='bx bx-home'></i>
         </template>
         {{$t('home')}}
       </vs-sidebar-item>
       <vs-sidebar-item id="Music" href="https://poolgo.medium.com/">
         <template #icon>
           <i class='bx bxs-news'></i>
         </template>
         {{$t('navNews')}}
       </vs-sidebar-item>
       <vs-sidebar-group>
         <template #header>
           <vs-sidebar-item arrow>
             <template #icon>
               <i class='bx bxs-contact'></i>
             </template>
             {{$t('navContact')}}
           </vs-sidebar-item>
         </template>

         <vs-sidebar-item id="twitter" href="https://twitter.com/PoolGoDefi">
           <template #icon>
             <i class='bx bxl-twitter' ></i>
           </template>
           Twitter
         </vs-sidebar-item>
         <vs-sidebar-item id="discord" href="https://discord.gg/x4WWrDd2tJ">
           <template #icon>
             <i class='bx bxl-discord'></i>
           </template>
           Discord
         </vs-sidebar-item>

         <vs-sidebar-item id="github" href="https://github.com/poolgo">
           <template #icon>
             <i class='bx bxl-github' ></i>
           </template>
           Github
         </vs-sidebar-item>

         <vs-sidebar-item id="medium" href="https://poolgo.medium.com/">
           <template #icon>
             <i class='bx bxl-medium' ></i>
           </template>
           Medium
         </vs-sidebar-item>
         <vs-sidebar-item id="telegram" href="https://t.me/poolgofinance">
           <template #icon>
             <i class='bx bxl-telegram' ></i>
           </template>
           Telegram
         </vs-sidebar-item>

       </vs-sidebar-group>

       <vs-sidebar-item id="Sponsor" href="https://poolgo.medium.com/how-to-sponsor-poolgo-ba1fa0cd11c7">
         <template #icon>
           <i class='bx bxs-donate-heart'></i>
         </template>
         {{$t('navSponsor')}}
       </vs-sidebar-item>
       <vs-sidebar-item id="Security" href="https://docs.poolgo.finance/poolgo-finance/risks">
         <template #icon>
           <i class='bx bx-bug'></i>
         </template>
         {{$t('navSecurity')}}
       </vs-sidebar-item>
       <vs-sidebar-item id="FAQ" href="https://docs.poolgo.finance/poolgo-finance/faq">
         <template #icon>
           <i class='bx bx-chat'></i>
         </template>
         {{$t('navFAQ')}}
       </vs-sidebar-item>
<!--       <vs-sidebar-item id="chat">-->
<!--         <template #icon>-->
<!--           <i class='bx bx-info-circle' ></i>-->
<!--         </template>-->
<!--         {{$t('navAbout')}}-->
<!--       </vs-sidebar-item>-->
       <template #footer>
       </template>
     </vs-sidebar>
     <vs-dialog width="300px" not-center prevent-close v-model="switchNetworkDialogShow">
       <template #header>
         <h4 class="not-margin">
           {{$t('noticeSentece5Part1')}}<b>{{$t('noticeSentece5Part2')}}</b>
         </h4>
       </template>

       <div class="con-content">
         <p>
           {{$t('noticeSentece6')}}

         </p>
       </div>

       <template #footer>
         <div class="con-footer">
           <vs-button @click="switchNetworkDialogShow=false" transparent>
             {{$t('noticeConfirm')}}
           </vs-button>
         </div>
       </template>
     </vs-dialog>
   </div>



</template>

<script>
  import { mapActions } from 'vuex'
  import collapse from '@/utils/collapse.js'
  import utils from '@/utils/utils.js'
  import {getLocalStorage,setLocalStorage} from '@/utils/local-storage.js'
  import config from "@/config";
  // import blockie from "../componentsMine/Blockie";
  export default {
    name: 'home-view',
    components: {
      collapse
    },
    data() {
      return {
        isActive: false,
        active: 'home',
        activeSidebar: false,
        token:'PCFX',
        switchNetworkDialogShow:false,
      }
    },
    computed: {

      balance(){
        if (this.token == 'PCFX') {
          return this.$store.state.web3.pcfxBalance.toFixed(1) + "PCFX"
        }else{
          return this.$store.state.web3.balance.toFixed(1) + "CFX"
        }
      },
      account() {
        return this.$store.state.web3.account
      }
    },
    watch: {
      '$store.state.web3.chainId': function () {
        let that = this;
        if ( that.$store.state.web3.chainId != parseInt(config.networkId)){
          that.switchNetworkDialogShow = true;
        
        }
      }
    },
    created(){
      let data = getLocalStorage('language');
      if (data!=null && data.language!=null){
        this.$i18n.locale = data.language
      }
      let tokenData = getLocalStorage('token');
      if (tokenData!=null && tokenData.token!=null){
        this.token = tokenData.token
      }
      // this.init()
    },
    methods: {
      ...mapActions(['cfxLogin','logout']),
      showTwitterPage(){
        window.location.href = ''
      },
      login() {
        let that = this;

        if (that.$store.state.web3.account == null) {
          that.cfxLogin();
        }
        if (that.$store.state.web3.isPortalInstalled == false) {
          that.$vs.notification({
            // width:'100%',
            duration: 10000,
            icon: "<i class='bx bx-error' ></i>",
            progress: 'auto',
            color: 'danger',
            position: 'top-center',
            title: that.$t('error'),
            text: that.$t('noticeWallet'),
          })
          return;
        }
      },
      exit(){
        this.logout();
      },
      shortAddr(account) {
        return utils.shortAddr(account)
      },
      switchBalance(command){
        this.token = command;
        setLocalStorage({token:command})
        // this.$vuetify.lang.current  = command
      },
      switchLanguage(command){
        this.$i18n.locale = command;
        setLocalStorage({language:command})
        // this.$vuetify.lang.current  = command
      },
      showPoolPage(){
        this.$router.push({
          path:'/pools'
        })
      },
    }
  }
</script>

<style>
  .collapse-menu {
    position: absolute;
    top: 80px;
    width: 100%;
    text-align: left;
  }
  .collapse-menu div {
    padding: 5px 10px;
    color: #76807F;
  }
  .heading {
    text-align: center;
    margin-bottom: 0px;
    background: #14151A;
  }
  .header-bar {
  	display: flex;
  	justify-content: space-between;
  	font-size: 1rem;
  	height: 70px;
  	padding-top: 20px;
  	padding-left: 20px;
  	padding-right: 20px;
  }
  .header-bar .el-button {
    -webkit-box-align: center;
    align-items: center;
    background-color: rgb(33, 33, 33);
    border: 0px;
    border-radius: 12px;
    color: rgb(144, 144, 255);
    cursor: pointer;
    display: flex;
    font-size: 14px;
    font-weight: 700;
    height: 36px;
    -webkit-box-pack: center;
    justify-content: center;
    outline: none;
    padding-left: 16px;
    padding-right: 16px;
    width: 120px;

  }
  .header-bar .logo {
  	height: 45px;
  }
  .header-bar .menu {
  	height: 45px;
    color: #eee;
    text-align: center;
    padding-top: 10px;
  }
  .header-bar .menu span {
    padding: 0 20px;
    font-weight: bold;
  }
  .header-bar .menu ul {
    padding: 0px;
  }
  .header-bar .menu ul li {
  	display: inline-block;
  	font-size: 1rem;
  	height: 45px;
  	padding: 15px;
  	font-weight: bold;
  }
  .logo-size{
    width:200px;
  }
  @media screen  and (max-width:600px) {
    .logo-size{
      width:78px;
    }
  }
</style>
